			disabled:false,
			num:60
		}
	},
	mounted() {
		
	},
	methods:{
		login(){
			uni.navigateTo({
				url:'set'
			})
		},
		postCode(){
			this.disabled = true;
			
			this.code=this.num+'s';
			setInterval(()=>{
